import React from 'react';
import data from '../data/data';
import Button from '@mui/material/Button';
import DeleteIcon from '@mui/icons-material/Delete';
import SendIcon from '@mui/icons-material/Send';
import Stack from '@mui/material/Stack';



function ContactPage() {
    return (<div className='content contact-card p-4 '>
        <h3>
            {data.ContactPage.title}
        </h3>
        <div className='row mx-0 my-4'>
        {
            data.ContactPage.info.map((e)=>{
            
                return<div key={e.name+"hhgg"} className='col-sm-12 col-md-6 col-lg-6 my-2 d-flex justify-center '>
                    <div className='row mx-0 contact-info w-100'>
                        <div className='col-sm-12 col-md-4 col-lg-4 text-center'>
                            {e.icon}
                        </div>
                        <div className='col-sm-12 col-md-8 col-lg-8'>
                            <h5>{e.name}</h5>
                            {e.value}
                        </div>
                    </div>
               
    
            </div>
            })
        }
             
             </div>
             <div className='form-card'>
                <div className='row mx-0 form-content'>
                    <div className='col-sm-12 col-md-6 col-lg-6  card-input1'>
                        <input type="text" placeholder='Your Name' />

                    </div>
                    <div className='col-sm-12 col-md-6 col-lg-6  card-input2'>
                        <input type="text" placeholder='Your Email' />

                    </div>

                </div>
                <div className='row mx-0 form-content'>
                    <div className='col-sm-12 col-md-12 col-lg-12 text-center card-input'>
                        <textarea rows={2}  type="text" placeholder='Subject' />

                    </div>

                </div>
                <div className='row mx-0 form-content'>
                    <div className='col-sm-12 col-md-12 col-lg-12 text-center '>
                        <textarea rows={4}  type="text" placeholder='Message' />

                    </div>
                    <div className='w-100 my-2 text-center'>
                            
                        <Button variant="contained"    endIcon={<SendIcon />}>
                            Send Message
                        </Button>
                     
                    
                </div>

                </div>
               
             </div>
    
    

    </div>);
}

export default ContactPage;