import * as React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import { useNavigate } from 'react-router-dom';

export default function ColorTabs({returnedSelected,changeValue}) {
    const navigate= useNavigate()
  const [value, setValue] = React.useState('one');
  const [color,setColor] = React.useState({
    one:"",
    two:"grey",
    three:"grey",
    four:"grey",
    five:"grey"
  })
  React.useEffect(()=>{
    setValue(changeValue)
    setColor({
        one:"grey",
        two:"grey",
        three:"grey",
        four:"grey",
        five:"grey",
        [changeValue]:""
    })
    
  },[changeValue,value])
  

  const handleChange = (event, newValue) => {
    returnedSelected(newValue)
    setValue(newValue);
    
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Tabs
        value={value}
        onChange={handleChange}
        textColor="secondary"
        indicatorColor="secondary"
        aria-label="secondary tabs example"

        
      >
        <Tab style={{color:color.one}} value="one" label="Home" />
        <Tab style={{color:color.two}} value="two" label="About" />
        {/* <Tab style={{color:color.three}} value="three" label="Portifilo" /> */}
        <Tab style={{color:color.four}} value="four" label="Resume" />
        <Tab style={{color:color.five}} value="five" label="Contact" />
        
      </Tabs>
    </Box>
  );
}