import React from 'react';
import { Nav } from 'rsuite';
import HomeIcon from '@rsuite/icons/legacy/Home';


const Navbar = ({ active, onSelect, ...props }) => {
    
  return (
    <Nav {...props} activeKey={active} onSelect={onSelect} style={sty.nav}>
      <Nav.Item style={sty.word}  eventKey="home" icon={<HomeIcon />}>
        Home
      </Nav.Item>
      <Nav.Item style={sty.word} eventKey="About">About</Nav.Item>
      <Nav.Item style={sty.word} eventKey="Resume">Resume</Nav.Item>
      <Nav.Item style={sty.word} eventKey="Services">Services</Nav.Item>
      <Nav.Item style={sty.word} eventKey="Contact">Contact</Nav.Item>
    </Nav>
  );
};
function NavBar() {
    const [active, setActive] = React.useState('home')
    return (<>
  
        {/* <Navbar active={active} onSelect={setActive} /> */}
        <Navbar  appearance="tabs" active={active} onSelect={setActive} />
        {/* <Navbar appearance="tabs" reversed active={active} onSelect={setActive} /> */}
        {/* <Navbar appearance="subtle" active={active} onSelect={setActive} /> */}
        {/* <Navbar appearance="subtle" reversed active={active} onSelect={setActive} /> */}
      </>  );
}


export default NavBar;

const sty={
    word:{
        color:'pink',
        
    },
    nav:{ fontSize:"1rem",background:"#000000b0",position:"fixed",zIndex:"9",width:"100vw",
    // height:"100%"
 }
}