import React from 'react';
import data from '../data/data';

const BasicCard = ({title,content})=>{
    return(<div className='col-sm-12 col-md-6 col-lg-6'>
    <h4 className='my-3'>{title}</h4>
    <div >
        {content.map((e)=>{
            return(<div className='resume-card  '>
                <div className='d-flex absolute_title'>
                    <div className='circular' ></div>
                    <h4>{e.subTitle}</h4>
                </div>
                <div className='border-card '>
                    <span>{e.date}</span>
                    {e.type.length>0 && <p>{e.type} ,{e.place}</p>}
                    
                    <p>{e.description}</p>
                    <ul>{e.list.map((e)=>{return(<li>{e}</li>)})
                    }</ul>
                </div>
            </div>)
        })}
    </div>
  
</div>)

} 
function ResumePage() {
    return ( <div className='content p-4 '>
        <h3>Check My Resume</h3>
        <div className='row mx-0'>
            
            {
                data.ResumePage.map((e)=>{
                    return(<BasicCard title={e.title} content={e.content} />)
                })
            }


        </div>

    </div> );
}

export default ResumePage;